body {
    margin: 0;
    font-family: Arial, sans-serif;
  }
  
  .header {
    background: #333;
    color: white;
    padding: 1rem;
    text-align: center;
  }
  
  .header nav {
    margin-top: 0.5rem;
  }
  
  .header nav a {
    margin: 0 10px;
    color: white;
    text-decoration: none;
    font-size: 1rem;
  }
  
  .section {
    padding: 2rem 1rem;
    text-align: center;
  }
  
  ul {
    list-style: none;
    padding: 0;
  }
  
  ul li {
    margin: 0.5rem 0;
  }
  
  form {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    max-width: 400px;
    margin: 1rem auto;
    width: 100%;
    padding: 0 1rem;
  }
  
  input, textarea {
    padding: 0.75rem;
    font-size: 1rem;
    width: 100%;
    box-sizing: border-box;
  }
  
  button {
    padding: 0.75rem;
    background: #333;
    color: white;
    border: none;
    cursor: pointer;
    font-size: 1rem;
  }
  
  button:hover {
    background: #555;
  }
  
  .footer {
    background: #f1f1f1;
    text-align: center;
    padding: 1rem;
    font-size: 0.9rem;
  }
  
  /* Responsive nav for smaller screens */
  @media (max-width: 600px) {
    .header nav a {
      display: block;
      margin: 0.5rem 0;
    }
  }  
  ul {
    list-style: none;
    padding: 0;
  }
  
  